*{
  margin:0;
  padding:0;
  box-sizing: border-box;
}

/*font-family: 'Poppins', sans-serif;*/
/*font-family: 'Playfair Display', serif;*/

/* styles.css or your stylesheet */
.footer {
  background:white;
  color: #333;
  padding: 20px 0;
  text-align: center;
  background: -webkit-linear-gradient(
    to right,
    #434343,
    #000000,
  );
  background: linear-gradient(to right, #434343, #000000);
}

.footer-title {
  font-size: 1.5rem;
  color:white;
}

.footer-link {
  color: #007bff;
  text-decoration: none;
  margin: 0 10px;
}

.footer-copyright {
  font-size: 0.9rem;
  color: slategray;
}

.footer a{
  text-decoration: none;
  padding: 10px;
}

/* Apply styles to the entire navigation bar */
.navbar {
  background-color: slategray; /* Transparent background with 60% opacity */
  border: none; /* No border to override Bootstrap styles */
  border-radius: 20px; /* Curved corners */
  padding: 5px 5px; /* Padding around the navigation bar */
  margin: 20px 20px; /* Margin on all sides */
}

/* Style for the brand link */
.navbar-brand {
  color: slateblue; /* White text color for the brand */
  font-size: 24px;
  font-weight: bold;
  font-weight: 700;
}

/* Hover effect for the brand link */
.navbar-brand .brand-text:hover {
  color: black; /* Text color on hover */
}

/* Style for the navigation links */
.nav-link {
  color: slateblue;
  font-size: 16px;
  text-decoration: none;
  border: 0.5px white;
  padding: 6px 10px; /* Adjust padding to control button size */
  margin: 0 5px;
  border-radius: 20px;
  transition: color 0.2s, background-color 0.2s;
}

/* Hover effect for navigation links */
.nav-link:hover {
  color: black;
  background-color: lightgray;
}

/* Media query for smaller devices */
@media only screen and (max-width: 768px) {
  .nav-link {
    padding: 2px; /* Removing padding */
    border-width: 2px;
    display: inline-block;
  }
}

/* Optional: Add a subtle box shadow to the navigation bar */
.navbar {
  box-shadow: 0 8px 6px -6px gray;
  font-size: 17px;
  line-height: 26px;
  border-bottom: 0.5px solid gray !important;
}

/* styling the title and logo */
.icon-text-container {
  display: flex;
  align-items: center;
}

.cart-icon {
  margin-right: 10px; /* Adjust the margin as needed */
}

.brand-text {
  font-size: 24px;
  font-weight: bold;
  color: slateblue;
}

/* Pagenotfound.css */

/* Styles for the container */
.pnf {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 70vh;
  text-align: center;
}

/* Style for the 404 title */
.pnf-title {
  font-size: 6rem;
  color: slateblue; /* Choose a color that matches your design */
}

/* Style for the heading */
.pnf-heading {
  font-size: 2rem;
  margin-top: 8px;
  color: #333; /* Adjust the color as needed */
}

/* Style for the "Go Back" button */
.pnf-btn{
  display: inline-block;
  background-color: slateblue;
  color:#fff;
  padding:12px 24px;
  border-radius: 5px;
  font-size: 20px;
  border:none;
  cursor: pointer;
  transition: 0.3s ease;
  text-decoration: none;
  margin-top: 15px;
  }

.pnf-btn:hover{
  background-color: slateblue;
  transform: scale(1.1);
}

/* styling about page */

.about{
  background-color:#FFFFE0; 
  overflow: hidden;
}

.about-heading h1{
  color: slateblue;
  font-size: 55px;
  text-align: center;
  margin-top: 35px;
}

.about-container{
  display: flex;
  justify-content: center;
  align-items: center;
  width: 90%;
  margin: 65px auto;
}

.about-content{
  flex: 1;
  width: 600px;
  margin: 0px 25px;
  animation: fadeInUp 2s ease;
}
.about-content h2{
  font-size: 38px;
  margin-bottom: 20px;
  color: #333;
}

.about-content p{
  font-size: 24px;
  line-height: 1.5;
  margin-bottom: 40px;
  color: #666;
}

.about-content button{
  display: inline-block;
  background-color: slateblue;
  color:#fff;
  padding:12px 24px;
  border-radius: 5px;
  font-size: 20px;
  border:none;
  cursor: pointer;
  transition: 0.3s ease;
  }

.about-content button:hover{
  background-color: slateblue;
  transform: scale(1.1);
}  

.about-image{
  flex:1;
  width: 600px;
  margin: auto;
  animation: fadeInRight 2s ease;
}

.about-image img {
  width: 100%;
  height: auto;
  border-radius: 10px;
}

.about-learn-more h3{
  font-size: 38px;
  margin-bottom: 20px;
  color: #333;
  margin-top: 30px;
}

.about-learn-more p{
  font-size: 24px;
  line-height: 1.5;
  margin-bottom: 40px;
  color: #666;
}

@media screen and (max-width: 768px){
  .about-heading h1{
    font-size: 45px;
    margin-top: 30px;
  }
  .about{
    margin: 0px;
  }
  .about-container{
    width: 100%;
    flex-direction: column;
    margin: 0px;
    padding: 0px 40px;
  }
  .about-content{
    width: 100%;
    margin: 35px 0px;
  }
  .about-content h2{
    font-size: 30px;
  }

  .about-learn-more h3{
    margin-top: 10px;
    font-size: 30px;
  }
  .about-content p{
    font-size: 18px;
    margin-bottom: 20px;
  }
  .about-content button{
    font-size: 16px;
    padding: 8px 16px;
  }
  .about-image{
    width: 100%;
  }
  
}

@keyframes fadeInUp {
  0%{
    opacity: 0;
    transform: translateY(50px);
  }
  100%{
    opacity: 1;
    transform: translateY(0px);
  }
}

@keyframes fadeInRight {
  0%{
    opacity: 0;
    transform: translateY(-50px);
  }
  100%{
    opacity: 1;
    transform: translateY(0px);
  }
}

/* Assume a common class name for the form and the image */

.contact{
  background-color:#FFFFE0; 
  overflow: hidden;
}

.contact-container img{
  border-radius: 10px;
  animation: fadeInRight 2s ease;
}

.contact-container {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 90%;
  margin: 65px auto;
  background-color:#FFFFE0;
}

.contact-form {
  background: #FFFFE0;
  display: flex;
  flex-direction: column;
  padding: 2vw 4vw;
  width: 90%;
  max-width: 600px;
  border-radius: 10px;
}

.contact-heading{
  color: slateblue;
  font-weight: 800;
  margin-bottom: 20px;
  align-items: center;
  text-align: center;
  margin-top: 20px;
  
}

.contact-form input,
.contact-form textarea {
  border: 0;
  margin: 10px 0;
  padding: 20px;
  outline: none;
  background: #f5f5f5;
  font-size: 16px;
}

.contact-form button {
  padding: 12px;
  background: slateblue;
  color: #fff;
  font-size: 18px;
  border: none;
  cursor: pointer;
  width: 150px;
  margin: 20px auto 0px;
  border-radius: 30px;
  align-items: center;
  transition: 0.3s ease;
}

.contact-form button:hover {
  background-color: slateblue;
  transform: scale(1.1);
}

/* For small devices (e.g., phones) */
@media screen and (max-width: 768px) {
  .contact-container {
    flex-direction: column;
    align-items: center;
  }

  .contact-form {
    width: 100%;
    padding: 10px;
    border-radius: 0;
  }

  .contact-heading {
    font-size: 20px;
  }

  .contact-form input,
  .contact-form textarea {
    padding: 10px;
    font-size: 14px;
  }

  .contact-form button {
    padding: 12px;
    font-size: 16px;
    width: 100%;
    margin: 20px 0 0;
  }

  .contact-container img {
    width: 100%;
    border-radius: 10px;
  }
}



