/* CSS for Register.js */
.form-heading {
  color: slateblue;
  text-align: center;
  padding: 10px;
}

form {
  max-width: 400px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
}

input {
  margin-bottom: 20px;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 25px;
  font-size: 16px;
}

input::placeholder {
  color: #999;
}

.btn {
  padding: 10px 20px;
  background-color: slateblue;
  color: white;
  border: none;
  border-radius: 25px;
  cursor: pointer;
  margin-bottom: 20px;
}

@media screen and (max-width: 768px) {
  .background {
    background-color: #f0f0f0; 
  }

  .form-heading {
    font-size: 28px;
  }

  form {
    max-width: 300px; 
  }

  input {
    padding: 8px; 
    font-size: 14px; 
  }

  .btn {
    padding: 8px 16px;
    font-size: 14px;
  }
}


/* styles.css */
.login-container {
  display: flex;
  flex-direction: column;
  align-items: center;
