  border: 1px solid #ccc;
  border-radius: 25px;
  font-size: 16px;
  width: 300px; /* Adjust width as needed */
}

.login-btn {
  background-color: slateblue;
  color: white;
  padding: 10px 20px;
  border: none;
  border-radius: 25px;
  cursor: pointer;
  width: 300px; /* Adjust width as needed */
}

.forgot-password-btn{
  background-color: white;
  color: slateblue;
  padding: 10px 20px;
  border: none;
  border-radius: 25px;
