/* admin dashboard/menu styling */

.row {
  background-color: white;
  margin-top: 10px;
  margin-bottom: 10px;
}

.col-md-3 {
  padding: 20px;
}

.col-md-9 {
  background-color: white;
  padding: 20px;
}

.list-group {
  border-radius: 15px;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.25);
  color: slateblue;
  margin-right: 50px;
  border-color: none;
}

.list-group-item {
  transition: all 0.2s ease-in-out;
  font-weight: 400;
}

.list-group-item:hover {
  background-color: lightgray;
}

.list-group-item.active {
  background-color: slateblue;
  color: white;
  font-weight: bold;
}

.card {
  border-radius: 10px;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.25);
  background: white;
  color: black;
  margin-top: 18px;
  padding-bottom: 30px;
  padding-top: 30px;
}

.card-header {
  background-color: #def3fe;
  font-weight: bold;
}

.card-body {
  font-weight: regular;
}

.list-group h3{
  color: black;
}

h4 {
  font-size: 20px;
  font-weight: regular;
}

p {
  font-size: 16px;
  font-weight: regular;
}

@media screen and (max-width: 768px) {
  .col-md-3 {
    padding: 10px;
  }

  .col-md-9 {
    padding: 10px;
  }

  .card {
    margin-top: 10px;
  }

  h4 {
    font-size: 18px;
  }

  p {
    font-size: 14px;
  }

  .list-group{
    margin-right: 30px;
  }
}

